import pageHeader from './pageHeader.vue'
import interfaces from '../../../utils/interfaces.js'
export default {
	components: {
		pageHeader
	},
	data() {
		return {
			swiperList: [],
			categoryList: [],
			promotion: [],
			currentSwiper: 0
		}
	},
	onLoad() {
		this.initData()
	},
	methods: {
		
		initData() {
			this.request({
				url: interfaces.getMallData,
				success: (res => {
					console.log(res)
					const {
						swiperList,
						categoryList,
						promotion
					} = res.data
					this.swiperList = swiperList
					this.categoryList = categoryList
					this.promotion = promotion
				})
			})
		},
		swiperChange(e) {
			// console.log(e.detail.current);
			this.currentSwiper = e.detail.current
		},
		handleCategory(item) {
			console.log(item)
			uni.navigateTo({
				url:'../../goods/goodsList?name=' + item.name
			})
		},
		handelPromotion(item){
			uni.showToast({
				title: item.title,
				icon: 'none'
			})
		}
	}
}
